import React, { useEffect, useState } from "react";
import "../../styles/Detail.css";
import { useLocation } from "react-router-dom";
import { message, Modal } from "antd";
import axios from "axios";
import config from "../../config";
import DataTable from "react-data-table-component";
import { LoadingOutlined } from "@ant-design/icons";
import { Flex, Spin } from "antd";
import moment from "moment";

const OrderDetail = () => {
  const location = useLocation();
  const order = location.state.row;
  const [orders, setorders] = useState();
  const [response, setResponse] = useState(null);

  const fetchStatistical = async () => {
    const userId = localStorage.getItem("userId");
    const accessToken = localStorage.getItem("accessToken");

    console.log(userId);
    console.log(accessToken);
    await axios
      .get(config.API_IP + `/admin/tradingHistory/${order._id}`, {
        headers: {
          "x-xclient-id": userId,
          authorization: accessToken,
        },
      })
      .then((res) => {
        setResponse(res);
        setorders(res.data.message.getTradeing[0]);
        console.log(res.data.message.getTradeing[0].productInfo[0]);
        console.log(res);
      });
  };
  useEffect(() => {
    fetchStatistical();
  }, []);

  const statusColor = (status) => {
    switch (status) {
      case "pending":
        return "orange";
      case "confirmed":
        return "cyan";
      case "shipped":
        return "blue";
      case "cancelled":
        return "red";
      case "delivered":
        return "green";
      default:
        return "black";
    }
  };
  const columns = [
    { name: "ID", selector: (row, index) => `#${index + 1}` },
    {
      name: "nh",
      selector: (row) => (
        <img className="row-image" src={`/uploads/${row.product_thumb[0]}`} />
      ),
    },
    {
      name: "Tn sn phm",
      selector: (row) => row.product_name,
      sortable: true,
    },
    {
      name: "Gi sn phm",
      selector: (row) =>
        row.product_price.toLocaleString("vi-VN", {
          style: "currency",
          currency: "VND",
        }),

      sortable: true,
    },
  ];
  const customHeader = {
    headCells: {
      style: {
        fontSize: "14px",
        fontWeight: "bold",
        backgroundColor: "#e0e0e0",
        border: "1px solid #ddd",
      },
    },

    cells: {
      style: {
        border: "1px solid #ddd",
      },
    },
  };
  return (
    <div>
      {response ? (
        <div
          style={{
            height: "100vh",
            display: "grid",
            gridTemplateColumns: "2fr 1fr",
          }}
        >
          <div>
            <div className="box">
              <p>TNG QUAN</p>

              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                  padding: "0px",
                }}
              >
                <div>
                  <div>
                    M theo di n hng : {orders.order_trackingNumber}
                  </div>
                  <div>
                    n hng :{" "}
                    <label
                      style={{ textTransform: "uppercase", color: "#0099ff" }}
                    >
                      {orders._id}
                    </label>
                  </div>

                  <div>
                    Thi gian t hng :{" "}
                    {moment(orders.createdAt).format("HH:mm:ss DD/MM/YYYY")}
                  </div>
                </div>
                <div
                  style={{
                    backgroundColor: statusColor(orders.order_status),
                    color: "#fff",
                    height: "55px",
                    borderRadius: "10px",
                    margin: "20px",
                  }}
                >
                  Trng thi n hng : {orders.order_status}
                </div>
              </div>
            </div>
            <div
              style={{
                flex: 1,
                display: "grid",
                gridTemplateColumns: "1fr 1fr",
              }}
            >
              <div className="box">
                <p>CA HNG</p>
                <div>
                  <label
                    style={{
                      fontWeight: "500",
                      fontSize: "15px",
                      textTransform: "uppercase",
                      width: "100%",
                    }}
                  >
                    {orders.shopInOrder[0].user_name}
                  </label>
                  Thng tin lin h: {orders.shopInOrder[0].email}
                </div>
              </div>
              <div className="box">
                <p>KHCH HNG </p>
                <div>
                  {" "}
                  <label
                    style={{
                      fontWeight: "500",
                      fontSize: "15px",
                      textTransform: "uppercase",
                      width: "100%",
                    }}
                  >
                    {orders.order_userId[0].fullName}
                  </label>
                  <label
                    style={{
                      width: "100%",
                    }}
                  >
                    S in thoi: 0{orders.order_userId[0].phoneNumber}
                  </label>
                  a ch nhn hng: {orders.order_shipping.City}
                </div>
              </div>
            </div>
            <div style={{ margin: "10px" }}>
              <DataTable
                columns={columns}
                data={orders.productInfo[0]}
                highlightOnHover
                customStyles={customHeader}
              />
            </div>
          </div>
          <div className="box pay">
            <p>THANH TON </p>
            <div>Phng thc thanh ton : VN PAY</div>
            <div>
              Gi tin :{" "}
              <label htmlFor="" style={{ color: "red" }}>
                {" "}
                {orders.order_checkout.totalCheckout.toLocaleString("vi-VN", {
                  style: "currency",
                  currency: "VND",
                })}
              </label>
            </div>
            <div>
              Gim gi :{" "}
              <label
                htmlFor=""
                style={{
                  color: "#00e600",
                }}
              >
                {orders.order_checkout.totalDiscount.toLocaleString("vi-VN", {
                  style: "currency",
                  currency: "VND",
                })}
              </label>
            </div>
            <div>
              Ph vn chuyn :{" "}
              <label htmlFor="" style={{ color: "red" }}>
                {orders.order_checkout.feeShip.toLocaleString("vi-VN", {
                  style: "currency",
                  currency: "VND",
                })}
              </label>
            </div>
            <div style={{ marginTop: "10px", borderTop: "1px solid #e0e0e0" }}>
              TNG :{" "}
              <label
                htmlFor=""
                style={{ color: "orange", fontWeight: "500", fontSize: "20px" }}
              >
                {orders.order_checkout.totalPrice.toLocaleString("vi-VN", {
                  style: "currency",
                  currency: "VND",
                })}
              </label>
            </div>
          </div>
        </div>
      ) : (
        <div
          style={{
            height: "100vh",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Spin
            indicator={
              <LoadingOutlined
                style={{
                  fontSize: 30,
                }}
                spin
              />
            }
          />
        </div>
      )}
    </div>
  );
};

export default OrderDetail;
