import React, { useEffect, useState } from "react";
import config from "../../config";
import axios from "axios";
import DataTable from "react-data-table-component";
import "../../styles/Row.css";
import { Button, message, Modal } from "antd";
import { SearchOutlined } from "@ant-design/icons";
import { LoadingOutlined } from "@ant-design/icons";
import { Spin } from "antd";
import unorm from "unorm";
const AddCategoryModal = ({
  open,
  onCancel,
  onSubmit,
  name,
  setName,
  thumb,
  setThumb,
  img,
}) => {
  return (
    <Modal
      title="Thm danh mc"
      open={open}
      onOk={onSubmit}
      onCancel={onCancel}
    >
      <div className="modal-container">
        <form>
          {(thumb || img) && (
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <img
                alt="not found"
                width={"140px"}
                height={"140px"}
                style={{
                  borderRadius: "50%",
                  objectFit: "cover",
                }}
                src={thumb ? URL.createObjectURL(thumb) : img}
              />
            </div>
          )}
          <label>
            <input
              type="file"
              onChange={(event) => {
                setThumb(event.target.files[0]);
              }}
              style={{ display: "none" }}
            />
            <div
              style={{
                position: "absolute",
                top: 160,
                left: 290,
                width: "40px",
                height: "40px",
              }}
            >
              <img
                src={require("../../assets/camera.png")}
                alt="Ti danh mc"
                style={{ width: "30px", height: "30px" }}
              />
            </div>
          </label>

          <label>Tn danh mc : </label>
          <input
            type="text"
            value={name}
            placeholder={"Vui lng nhp danh mc"}
            onChange={(e) => setName(e.target.value)}
          />
          <div style={{ flexDirection: "row", width: "100%" }}></div>
        </form>
      </div>
    </Modal>
  );
};

const UpdateCategoryModal = ({
  open,
  onCancel,
  onSubmit,
  setName,
  setThumb,
  selected,
}) => {
  const [updatedCategoryName, setUpdatedCategoryName] = useState(
    selected ? selected.category_name : ""
  );

  const [modalThumb, setModalThumb] = useState(
    selected ? selected.category_thumb : ""
  );

  useEffect(() => {
    if (selected) {
      setUpdatedCategoryName(selected.category_name);
      setModalThumb(selected.category_thumb);
    }
  }, [selected]);

  const handleNameChange = (e) => {
    setUpdatedCategoryName(e.target.value);
    setName(e.target.value);
  };

  return (
    <Modal title="Sa danh mc" open={open} onOk={onSubmit} onCancel={onCancel}>
      <div className="modal-container">
        <form>
          <div
            style={{
              width: "100%",
              display: "flex",
              justifyContent: "center",
              padding: "30px",
              height: "140px",
              borderRadius: "50%",
            }}
          >
            <img
              alt="not found"
              style={{
                objectFit: "cover",
              }}
              src={modalThumb}
            />
          </div>
          <label>
            <input
              type="file"
              onChange={(event) => {
                // Update the modalThumb state with the selected file
                setModalThumb(URL.createObjectURL(event.target.files[0]));
                // Update the parent component's state (setThumb)
                setThumb(event.target.files[0]);
              }}
              style={{ display: "none" }}
            />
            <div
              style={{
                position: "absolute",
                top: 160,
                left: 290,
                width: "40px",
                height: "40px",
              }}
            >
              <img
                src={require("../../assets/camera.png")}
                alt="Ti danh mc"
                style={{ width: "30px", height: "30px" }}
              />
            </div>
          </label>
          <label>Tn danh mc : </label>
          <input
            type="text"
            value={updatedCategoryName}
            placeholder={"Vui lng nhp danh mc"}
            onChange={handleNameChange}
          />
          <div style={{ flexDirection: "row", width: "100%" }}></div>
        </form>
      </div>
    </Modal>
  );
};

const DeleteCategoryModal = ({ open, onCancel, onOk, selected }) => {
  return (
    <Modal
      title="Xa danh mc"
      open={open}
      onOk={onOk}
      onCancel={onCancel}
      okButtonProps={{ style: { background: "red", borderColor: "red" } }}
    >
      <p>Bn mun xa danh mc "{selected.category_name}" ny ch?</p>
    </Modal>
  );
};

const Categories = () => {
  const [openModal, setOpenModal] = useState(false);
  const [openModalDelete, setOpenModalDelete] = useState(false);
  const [openModalUpdate, setOpenModalUpdate] = useState(false);
  const [categories, setCategories] = useState([]);
  const [selected, setSelected] = useState({});
  const [name, setName] = useState("");
  const [messageApi, contextHolder] = message.useMessage();
  const [img, setImg] = useState(
    "https://st2.depositphotos.com/1561359/12101/v/950/depositphotos_121012076-stock-illustration-blank-photo-icon.jpg"
  );
  const [thumb, setThumb] = useState(null);
  const [response, setResponse] = useState(null);

  const [search, setSearch] = useState("");
  const [filteredCategories, setFilteredCategories] = useState([]);

  const fetchData = () => {
    const userId = localStorage.getItem("userId");
    const accessToken = localStorage.getItem("accessToken");
    axios
      .get(config.API_IP + "/admin/category", {
        headers: {
          "x-xclient-id": userId,
          authorization: accessToken,
        },
      })
      .then((res) => {
        setCategories(res.data.message);
        setResponse(res);
        console.log(res.data.message);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  useEffect(() => {
    fetchData();
  }, []);

  const columns = [
    { name: "ID", selector: (row, index) => `#${index + 1}` },
    {
      name: "nh",
      selector: (row) => (
        <img
          className="row-image"
          style={{ padding: "15px", objectFit: "contain" }}
          src={row.category_thumb}
        />
      ),
    },
    {
      name: "Tn danh mc",
      selector: (row) => row.category_name,
      sortable: true,
    },
    {
      name: "Action",
      selector: (row) => (
        <div>
          <a
            style={{ color: "green", cursor: "pointer", fontSize: "14px" }}
            onClick={() => {
              setOpenModalUpdate(true);
              setSelected(row);
            }}
          >
            Update
          </a>
          <a
            style={{
              color: "red",
              cursor: "pointer",
              marginLeft: "15px",
              fontSize: "14px",
            }}
            onClick={() => {
              setOpenModalDelete(true);
              setSelected(row);
            }}
          >
            {" "}
            Delete
          </a>
        </div>
      ),
    },
  ];

  const customHeader = {
    headCells: {
      style: {
        fontSize: "14px",
        fontWeight: "bold",
        backgroundColor: "#e0e0e0",
        minWidth: "50px",
        border: "1px solid #ddd",
      },
    },
    rows: {
      style: {
        border: "1px solid #ddd",
      },
    },
    cells: {
      style: {
        border: "1px solid #ddd",
      },
    },
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (name.length <= 0 || !thumb) {
      await messageApi.open({
        type: "error",
        content: "in  thng tin!",
      });
      return;
    }
    const userId = localStorage.getItem("userId");
    const accessToken = localStorage.getItem("accessToken");
    try {
      const formData = new FormData();
      formData.append("category_name", name);
      formData.append("thumb", thumb);
      await axios.post(config.API_IP + `/category/createCategory`, formData, {
        headers: {
          "x-xclient-id": userId,
          authorization: accessToken,
        },
      });
      setOpenModal(false);
      fetchData();
      await messageApi.open({
        type: "success",
        content: "Thnh cng!",
      });
    } catch (error) {
      await messageApi.open({
        type: "error",
        content: "Tht bi!",
      });
      console.log("Failed" + error);
    }
  };
  const handleSubmitUpdate = async (e) => {
    e.preventDefault();
    if (name.length <= 0 || !thumb) {
      await messageApi.open({
        type: "error",
        content: "in  thng tin!",
      });
      return;
    }
    const userId = localStorage.getItem("userId");
    const accessToken = localStorage.getItem("accessToken");
    try {
      const formData = new FormData();
      formData.append("category_name", name);
      formData.append("thumb", thumb);
      formData.append("id_category", selected._id);
      await axios.put(config.API_IP + `/category/updateCategory`, formData, {
        headers: {
          "x-xclient-id": userId,
          authorization: accessToken,
        },
      });
      setOpenModalUpdate(false);
      fetchData();
      await messageApi.open({
        type: "success",
        content: "Thnh cng!",
      });
    } catch (error) {
      await messageApi.open({
        type: "error",
        content: "Tht bi!",
      });
      console.log("Failed" + error);
    }
  };
  const handleSubmitDelete = async (e) => {
    e.preventDefault();
    const userId = localStorage.getItem("userId");
    const accessToken = localStorage.getItem("accessToken");
    try {
      const formData = new FormData();
      formData.append("id_category", selected._id);
      await axios.post(config.API_IP + `/category/deleteCategory`, formData, {
        headers: {
          "x-xclient-id": userId,
          authorization: accessToken,
        },
      });
      setOpenModalDelete(false);
      fetchData();
      await messageApi.open({
        type: "success",
        content: "Thnh cng!",
      });
    } catch (error) {
      console.log(selected._id);
      await messageApi.open({
        type: "error",
        content: "Tht bi!",
      });
      console.log("Failed" + error);
    }
  };

  useEffect(() => {
    const normalizedSearchKey = unorm.nfd(search.toLowerCase());
    const result = categories.filter((item) => {
      const normalizedItem = unorm.nfd(item.category_name.toLowerCase());
      return search.length !== 0
        ? normalizedItem.includes(normalizedSearchKey)
        : true;
    });
    setFilteredCategories(result);
  }, [search, categories]);

  return (
    <div>
      {response ? (
        <div className="selling">
          {contextHolder}
          <AddCategoryModal
            open={openModal}
            onCancel={() => setOpenModal(false)}
            onSubmit={(e) => handleSubmit(e)}
            name={name}
            setName={setName}
            thumb={thumb}
            setThumb={setThumb}
            img={img}
          />

          <UpdateCategoryModal
            open={openModalUpdate}
            onCancel={() => setOpenModalUpdate(false)}
            onSubmit={(e) => handleSubmitUpdate(e)}
            name={name}
            setName={setName}
            thumb={thumb}
            setThumb={setThumb}
            img={img}
            selected={selected}
          />

          <DeleteCategoryModal
            open={openModalDelete}
            onCancel={() => setOpenModalDelete(false)}
            onOk={(e) => handleSubmitDelete(e)}
            selected={selected}
          />

          <div style={{ marginBottom: "10px" }}>
            <p className="title_page">Danh mc</p>
          </div>

          <DataTable
            columns={columns}
            data={filteredCategories}
            pagination
            responsive
            paginationPerPage={6}
            highlightOnHover
            customStyles={customHeader}
            striped
            subHeader
            subHeaderComponent={
              <div
                style={{
                  flex: 1,
                  display: "flex",
                  justifyContent: "space-between",

                  alignItems: "center",
                }}
              >
                <Button
                  type="primary"
                  onClick={() => {
                    setOpenModal(!openModal);
                    setName("");
                    setThumb("");
                  }}
                >
                  Thm danh mc
                </Button>
                <div
                  style={{
                    position: "relative",
                    flex: "1",
                    marginLeft: "10px",
                  }}
                >
                  <input
                    type="text"
                    className="search-input"
                    form-control
                    placeholder="Nhp t kha tm kim..."
                    value={search}
                    onChange={(e) => setSearch(e.target.value)}
                  />
                  <SearchOutlined
                    style={{
                      position: "absolute",
                      right: "10px",
                      top: "50%",
                      transform: "translateY(-50%)",
                      fontSize: "20px",
                      color: "black",
                    }}
                  />
                </div>
              </div>
            }
          />
        </div>
      ) : (
        <div
          style={{
            height: "100vh",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Spin
            indicator={
              <LoadingOutlined
                style={{
                  fontSize: 30,
                }}
                spin
              />
            }
          />
        </div>
      )}
    </div>
  );
};

export default Categories;
