import React, { useEffect, useState } from "react";
import axios from "axios";
import DataTable from "react-data-table-component";
import { Modal } from "antd";
import { SearchOutlined } from "@ant-design/icons";
import "../../styles/Row.css";
import config from "../../config";
import { Link } from "react-router-dom";
import { LoadingOutlined } from "@ant-design/icons";
import { Flex, Spin } from "antd";

const Shops = () => {
  const [shops, setShops] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selected, setSelected] = useState({});

  const [search, setSearch] = useState("");
  const [response, setResponse] = useState(null);

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const userId = localStorage.getItem("userId");
        const accessToken = localStorage.getItem("accessToken");
        const response = await axios.get(config.API_IP + "/admin/shop", {
          headers: {
            "x-xclient-id": userId,
            authorization: accessToken,
          },
        });
        setShops(response.data.message.mergedData);
        setResponse(response);
        console.log(response.data.message);
      } catch (error) {
        console.log(error);
      }
    };

    fetchData();
  }, []);

  const columns = [
    { name: "ID", selector: (row, index) => `#${index + 1}` },
    {
      name: "nh",
      selector: (row) => (
        <>
          {row.avatarShop ? (
            <img className="row-image" src={row.avatarShop} />
          ) : (
            <img
              src={require("../../assets/no-pictures.png")} // Replace with the correct path
            />
          )}
        </>
      ),
    },

    {
      name: "Tn ca hng",
      selector: (row) => row.nameShop,
      sortable: true,
    },
    {
      name: "Email",
      selector: (row) => row.emailShop,
      sortable: true,
    },
    {
      name: "S in thoi",
      selector: (row) => `0${row.phoneNumberShop}`,
      sortable: true,
    },
    {
      name: "a ch",
      selector: (row) => row.address,
      sortable: true,
    },
    {
      name: "Trng thi",
      selector: (row) => (
        <div>
          {!row.disable ? (
            <p style={{ color: "green" }}>Hin th</p>
          ) : (
            <p style={{ color: "red" }}>n</p>
          )}
        </div>
      ),
      sortable: true,
    },

    {
      name: "Action",
      selector: (row) => (
        <Link to={`/shops/${row.nameShop}`} state={{ row }}>
          View detail
        </Link>
      ),
    },
  ];
  const customHeader = {
    headCells: {
      style: {
        fontSize: "14px",
        fontWeight: "bold",
        backgroundColor: "#e0e0e0",
        border: "1px solid #ddd",
      },
    },
    rows: {
      style: {
        borderBottom: "1px solid #ddd",
      },
    },
    cells: {
      style: {
        border: "1px solid #ddd",
      },
    },
  };

  const [filteredShop, setFilteredShop] = useState([]);

  useEffect(() => {
    const result = shops.filter((item) => {
      return search.length !== 0
        ? item.nameShop.toUpperCase().includes(search.toUpperCase())
        : true;
    });
    setFilteredShop(result);
  }, [search, shops]);

  return (
    <div className="selling">
      {response ? (
        <>
          {" "}
          <div style={{ marginBottom: "10px" }}>
            <p className="title_page">Ca hng</p>
          </div>
          <Modal
            title="Thng tin ca hng"
            open={isModalOpen}
            onOk={handleOk}
            onCancel={handleCancel}
          >
            {isModalOpen == true ? (
              <div>
                <img className="detail_image" src={selected.avatarShop} />
                <div>
                  <p>Id : {selected._id}</p>
                  <p>Tn ca hng : {selected.nameShop}</p>
                  <p>Email : {selected.emailShop}</p>
                  <p>Lin h : 0{selected.phoneNumberShop}</p>
                  <p>a ch : {selected.address}</p>
                  <p>Gii thiu : {selected.des}</p>
                </div>
              </div>
            ) : (
              <div></div>
            )}
          </Modal>
          <DataTable
            columns={columns}
            data={filteredShop}
            pagination
            responsive
            paginationPerPage={6}
            highlightOnHover
            customStyles={customHeader}
            striped
            subHeader
            subHeaderComponent={
              <div
                style={{
                  flex: 1,
                  display: "flex",
                  justifyContent: "space-between",

                  alignItems: "end",
                }}
              >
                <div
                  style={{
                    position: "relative",
                    flex: "1",
                    marginLeft: "10px",
                  }}
                >
                  <input
                    type="text"
                    className="search-input"
                    form-control
                    placeholder="Nhp t kha tm kim..."
                    value={search}
                    onChange={(e) => setSearch(e.target.value)}
                  />
                  <SearchOutlined
                    style={{
                      position: "absolute",
                      right: "10px",
                      top: "50%",
                      transform: "translateY(-50%)",
                      fontSize: "20px",
                      color: "black",
                    }}
                  />
                </div>
              </div>
            }
          />
        </>
      ) : (
        <div
          style={{
            height: "100vh",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Spin
            indicator={
              <LoadingOutlined
                style={{
                  fontSize: 24,
                }}
                spin
              />
            }
          />
        </div>
      )}
    </div>
  );
};

export default Shops;
