import React, { useEffect, useState } from "react";
import axios from "axios";
import config from "../../config";
import slug from "slugifi";
import DataTable from "react-data-table-component";
import { LoadingOutlined } from "@ant-design/icons";
import { Flex, Spin } from "antd";
import { Button, message, Modal } from "antd";

import moment from "moment";
import { SearchOutlined } from "@ant-design/icons";
import unorm from "unorm";

const Event = () => {
  const [discount, setDiscount] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selected, setSelected] = useState({});
  const [search, setSearch] = useState("");
  const [filteredDiscount, setFilteredDiscount] = useState([]);
  const [openModal, setOpenModal] = useState(false);
  const [response, setResponse] = useState(null);
  const [messageApi, contextHolder] = message.useMessage();

  const fetchData = () => {
    const userId = localStorage.getItem("userId");
    const accessToken = localStorage.getItem("accessToken");
    axios
      .get(config.API_IP + "/admin/discount", {
        headers: {
          "x-xclient-id": userId,
          authorization: accessToken,
        },
      })
      .then((res) => {
        console.log(res.data.message);
        setResponse(res);
        setDiscount(res.data.message);
      });
  };

  const AddDiscountModal = ({ open, onCancel }) => {
    const [name, setName] = useState("");
    const [code, setCode] = useState("");
    const [value, setValue] = useState(0);
    const [quantity, setQuantity] = useState(0);
    const [minValue, setMinValue] = useState(0);
    const [start, setStart] = useState("");
    const [end, setEnd] = useState("");
    const [des, setDes] = useState("");
    const [maxUserPerUses, setMaxUserPerUses] = useState(0);
    const handleSubmit = async () => {
      const userId = localStorage.getItem("userId");
      const accessToken = localStorage.getItem("accessToken");
      if (
        !name ||
        !code ||
        !value ||
        !quantity ||
        !minValue ||
        !start ||
        !end ||
        !des
      ) {
        await messageApi.open({
          type: "warning",
          content: "Vui lng nhp  thng tin!",
        });
        return;
      }
      const formData = {
        name: name,
        code: code,
        value: value,
        max_uses: quantity,
        min_order_value: minValue,
        max_uses_per_user: maxUserPerUses,
        uses_count: 0,
        start_date: start,
        end_date: end,
        des: des,
        applies_to: "all",
      };
      await axios
        .post(`${config.API_IP}/discount`, formData, {
          headers: {
            "x-xclient-id": userId,
            authorization: accessToken,
          },
        })
        .then((res) => {
          messageApi.open({
            type: "success",
            content: "Thm thnh cng!",
          });
          fetchData();
          console.log(res);
        })
        .catch((e) => {
          messageApi.open({
            type: "error",
            content: "Thm tht bi!",
          });

          console.log(e);
        });
    };
    return (
      <Modal
        title="Thm m gim gi"
        open={open}
        onCancel={onCancel}
        onOk={() => handleSubmit()}
      >
        <div className="modal-container">
          <form>
            <label>Tn : </label>
            <input
              type="text"
              placeholder={"Vui lng nhp tn m gim gi"}
              onChange={(e) => setName(e.target.value)}
            />
            <label>M code : </label>
            <input
              type="text"
              placeholder={"Vui lng nhp code m gim gi"}
              onChange={(e) => setCode(e.target.value)}
            />
            <label>Gi tr : </label>
            <input
              type="number"
              placeholder={"Vui lng nhp s tin gim"}
              onChange={(e) => setValue(e.target.value)}
            />
            <label>S lng : </label>
            <input
              type="number"
              placeholder={"Vui lng nhp s lng m"}
              onChange={(e) => setQuantity(e.target.value)}
            />
            <label>S lng dng ti a: </label>
            <input
              type="number"
              placeholder={"Vui lng nhp s lng m"}
              onChange={(e) => setMaxUserPerUses(e.target.value)}
            />
            <label>Gi tr ti thiu : </label>
            <input
              type="number"
              placeholder={"Vui lng nhp gi tr ti thiu s dng m"}
              onChange={(e) => setMinValue(e.target.value)}
            />
            <label>Bt u : </label>
            <input type="date" onChange={(e) => setStart(e.target.value)} />
            <label>Kt thc : </label>
            <input type="date" onChange={(e) => setEnd(e.target.value)} />
            <label>Chi tit : </label>
            <input
              type="text"
              placeholder={"Vui lng nhp chi tit m gim gi"}
              onChange={(e) => setDes(e.target.value)}
            />
          </form>
        </div>
      </Modal>
    );
  };

  const showModal = () => {
    setIsModalOpen(true);
  };
  const handleOk = () => {
    setIsModalOpen(false);
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };
  useEffect(() => {
    fetchData();
  }, []);
  const columns = [
    { name: "ID", selector: (row, index) => `#${index + 1}` },

    {
      name: "Tn sn phm",
      selector: (row) => row.discount_name,
      sortable: true,
    },

    {
      name: "S lng",
      selector: (row) => row.discount_code,
      sortable: true,
    },
    {
      name: "Bt u",
      selector: (row) =>
        moment(row.discount_start_date).format("HH:mm:ss DD/MM/YYYY"),
      sortable: true,
    },
    {
      name: "Kt thc",
      selector: (row) =>
        moment(row.discount_end_date).format("HH:mm:ss DD/MM/YYYY"),
      sortable: true,
    },
    {
      name: "Action",
      selector: (row) => (
        <a
          className="view-detail"
          onClick={() => {
            showModal();
            setSelected(row);
          }}
        >
          View detail
        </a>
      ),
    },
  ];
  const customHeader = {
    headCells: {
      style: {
        fontSize: "14px",
        fontWeight: "bold",
        backgroundColor: "#e0e0e0",
      },
    },
    rows: {
      style: {
        border: "1px solid #ddd",
      },
    },
    cells: {
      style: {
        border: "1px solid #ddd",
      },
    },
  };
  useEffect(() => {
    const normalizedSearchKey = unorm.nfd(search.toLowerCase());
    const result = discount.filter((item) => {
      const normalizedItem = unorm.nfd(item.discount_name.toLowerCase());
      return search.length !== 0
        ? normalizedItem.includes(normalizedSearchKey)
        : true;
    });
    setFilteredDiscount(result);
  }, [search, discount]);
  return (
    <div>
      {response ? (
        <div className="selling">
          {contextHolder}
          <AddDiscountModal
            open={openModal}
            onCancel={() => setOpenModal(false)}
          />
          <p className="title_page">Gim gi</p>
          <Modal
            title="Thng tin gim gi"
            open={isModalOpen}
            onOk={handleOk}
            onCancel={handleCancel}
            style={{ textAlign: "center" }}
            bodyStyle={{
              textAlign: "left",
              marginTop: "20px",
              width: "90vw",
              marginLeft: "10px",
            }}
          >
            {isModalOpen == true ? (
              <div>
                <p>Tn m gim gi : {selected.discount_name}</p>
                <p>M gim gi : {selected.discount_code}</p>

                <p>Chi tit : {selected.discount_des}</p>
                <p>
                  Gim :{" "}
                  {selected.discount_value.toLocaleString("vi-VN", {
                    style: "currency",
                    currency: "VND",
                  })}
                </p>
                <p>
                  Gim cho sn phm trn:{" "}
                  {selected.discount_min_order_value.toLocaleString("vi-VN", {
                    style: "currency",
                    currency: "VND",
                  })}
                </p>
                <p>S lng cn li: {selected.discount_max_uses}</p>
                <p>
                  Bt u :{" "}
                  {moment(selected.discount_start_date).format(
                    "HH:mm:ss DD/MM/YYYY"
                  )}
                </p>
                <p>
                  Kt thc :{" "}
                  {moment(selected.discount_end_date).format(
                    "HH:mm:ss DD/MM/YYYY"
                  )}
                </p>
                <p>Loi m : {selected.discount_type}</p>
              </div>
            ) : (
              <div></div>
            )}
          </Modal>

          <DataTable
            columns={columns}
            data={filteredDiscount}
            pagination
            responsive
            paginationPerPage={10}
            highlightOnHover
            customStyles={customHeader}
            striped
            subHeader
            subHeaderComponent={
              <div
                style={{
                  flex: 1,
                  display: "flex",
                  justifyContent: "space-between",

                  alignItems: "center",
                }}
              >
                <Button type="primary" onClick={() => setOpenModal(!openModal)}>
                  Thm gim gi
                </Button>
                <div
                  style={{
                    position: "relative",
                    flex: "1",
                    marginLeft: "10px",
                  }}
                >
                  <input
                    type="text"
                    className="search-input"
                    form-control
                    placeholder="Nhp t kha tm kim..."
                    value={search}
                    onChange={(e) => setSearch(e.target.value)}
                  />
                  <SearchOutlined
                    style={{
                      position: "absolute",
                      right: "10px",
                      top: "50%",
                      transform: "translateY(-50%)",
                      fontSize: "20px",
                      color: "black",
                    }}
                  />
                </div>
              </div>
            }
          />
        </div>
      ) : (
        <div
          style={{
            height: "100vh",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Spin
            indicator={
              <LoadingOutlined
                style={{
                  fontSize: 30,
                }}
                spin
              />
            }
          />
        </div>
      )}
    </div>
  );
};

export default Event;
