import React, { useEffect, useState } from "react";
import axios from "axios";
import config from "../../config";
import DataTable from "react-data-table-component";
import "../../styles/Row.css";
import "../../styles/Modal.css";
import { SearchOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import { LoadingOutlined } from "@ant-design/icons";
import { Flex, Spin } from "antd";

const OrderScreen = () => {
  const [search, setSearch] = useState("");
  const [filteredOrders, setFilteredOrders] = useState([]);
  const [orders, setOrder] = useState([]);
  const [response, setResponse] = useState(null);
  const [selected, setSelected] = useState("");
  const status = [
    {
      vie: "Cha xc nhn",
      eng: "pending",
    },
    { vie: "Xc nhn", eng: "confirmed" },
    { vie: "ang giao", eng: "shipped" },
    { vie: " hy", eng: "cancelled" },
    { vie: " nhn", eng: "delivered" },
  ];
  useEffect(() => {
    const fetchData = () => {
      const userId = localStorage.getItem("userId");
      const accessToken = localStorage.getItem("accessToken");
      axios
        .get(config.API_IP + "/admin/order", {
          headers: {
            "x-xclient-id": userId,
            authorization: accessToken,
          },
        })
        .then((res) => {
          console.log(res.data.message);
          setResponse(res);
          setOrder(res.data.message);
        });
    };
    fetchData();
  }, []);

  //
  const selectedData = (row) => {
    const userId = localStorage.getItem("userId");
    const accessToken = localStorage.getItem("accessToken");
    axios
      .get(config.API_IP + "/admin/tradingHistory/" + row._id, {
        headers: {
          "x-xclient-id": userId,
          authorization: accessToken,
        },
      })
      .then((res) => {
        console.log(res.data.message);
      });
  };
  const statusColor = (status) => {
    switch (status) {
      case "pending":
        return "orange";
      case "confirmed":
        return "cyan";
      case "shipped":
        return "blue";
      case "cancelled":
        return "red";
      case "delivered":
        return "green";
      default:
        return "black";
    }
  };
  const statusText = (status) => {
    switch (status) {
      case "pending":
        return "Cha xc nhn";
      case "confirmed":
        return " xc nhn";
      case "shipped":
        return "ang giao";
      case "cancelled":
        return " hy";
      case "delivered":
        return " nhn";
      default:
        return "Khng xc nh";
    }
  };
  //
  const columns = [
    { name: "ID", selector: (row, index) => `#${index + 1}` },

    {
      name: "SDT khch hng",
      selector: (row) => `0${row.order_userId[0].phoneNumber}`,
      sortable: true,
    },
    {
      name: "Tn ca hng",
      selector: (row) => row.order_products[0].shopId.nameShop,
      sortable: true,
    },
    {
      name: "Sn phm",
      selector: (row) => row.item_products[0].productId.product_name,
      sortable: true,
    },
    ,
    {
      name: "Tng tin",
      selector: (row) =>
        row.item_products[0].price.toLocaleString("vi-VN", {
          style: "currency",
          currency: "VND",
        }),
      sortable: true,
    },
    {
      name: "Trng thi",
      selector: (row) => (
        <p style={{ color: statusColor(row.order_status) }}>
          {statusText(row.order_status)}
        </p>
      ),
    },

    {
      name: "Action",
      selector: (row) => (
        <Link to={`/order/${row._id}`} state={{ row }}>
          View detail
        </Link>
      ),
    },
  ];
  const customHeader = {
    headCells: {
      style: {
        fontSize: "14px",
        fontWeight: "bold",
        backgroundColor: "#e0e0e0",
        border: "1px solid #ddd",
      },
    },
    rows: {
      style: {
        border: "1px solid #ddd",
      },
    },
    cells: {
      style: {
        border: "1px solid #ddd",
      },
    },
  };

  useEffect(() => {
    const result = orders.filter((item) => {
      const matchesSearch =
        search.length !== 0
          ? item.order_userId.user_name
              .toUpperCase()
              .includes(search.toUpperCase())
          : true;
      const matchesStatus =
        selected.length !== 0 ? item.order_status === selected : true;
      return matchesSearch && matchesStatus;
    });

    setFilteredOrders(result);
  }, [search, selected, orders]);

  return (
    <div>
      {response ? (
        <div className="selling">
          <p className="title_page" style={{ marginBottom: "10px" }}>
            Order
          </p>
          <DataTable
            columns={columns}
            data={filteredOrders}
            pagination
            responsive
            paginationPerPage={9}
            highlightOnHover
            customStyles={customHeader}
            striped
            subHeader
            subHeaderComponent={
              <div
                style={{
                  flex: 1,
                  display: "flex",
                  justifyContent: "space-between",

                  alignItems: "center",
                }}
              >
                <div
                  style={{
                    position: "relative",
                    flex: "1",
                    marginLeft: "10px",
                  }}
                >
                  <select
                    value={selected}
                    className="filter-dropdown-select"
                    style={{ marginRight: "30px" }}
                    onChange={(e) => setSelected(e.target.value)}
                  >
                    <option value="">Tt c</option>
                    {status.map((st) => (
                      <option key={st.eng} value={st.eng}>
                        {st.vie}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
            }
          />
        </div>
      ) : (
        <div
          style={{
            height: "100vh",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Spin
            indicator={
              <LoadingOutlined
                style={{
                  fontSize: 30,
                }}
                spin
              />
            }
          />
        </div>
      )}
    </div>
  );
};

export default OrderScreen;
