import React, { useEffect, useState } from "react";
import "../../styles/Detail.css";
import { useLocation, useNavigate } from "react-router-dom";
import { Button, message, Modal } from "antd";
import axios from "axios";
import config from "../../config";

const ProductDetail = () => {
  const location = useLocation();
  const product = location.state.row;
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [messageApi, contextHolder] = message.useMessage();
  const nav = useNavigate();

  const handlerUnPublish = async () => {
    const userId = await localStorage.getItem("userId");
    const accessToken = await localStorage.getItem("accessToken");
    const formData = {
      shopId: product.product_shop._id,
      content: "Sn phm  b v hiu ha do vi phm nguyn tc sn phm !",
      productId: product._id,
    };
    console.log(formData);
    await axios.post(
      config.API_IP + "/admin/unpublishedProductByAdmin",
      formData,
      {
        headers: {
          "x-xclient-id": userId,
          authorization: accessToken,
        },
      }
    );
    await messageApi.open({
      type: "success",
      content: "Thnh cng",
    });
    setIsModalOpen(false);
    nav("/products");
  };
  console.log(product);
  const showModal = () => {
    setIsModalOpen(true);
  };
  const handleOk = () => {
    handlerUnPublish();
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };
  return (
    <div>
      {contextHolder}
      <Modal
        title="Log out"
        open={isModalOpen}
        onOk={handleOk}
        onCancel={handleCancel}
        okButtonProps={{ style: { background: "red", borderColor: "red" } }}
      >
        <p>Bn c chc chn mun n sn phm ny khng ?</p>
      </Modal>
      <div className="detail_container">
        <div className="detail_container_image">
          <img
            className="detail_image"
            src={`/uploads/${product.product_thumb[0]}`}
          />
          {product.product_thumb.length > 0 ? (
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                width: "400px",
                justifyContent: "space-between",
                margin: "10px 0px",
              }}
            >
              {product.product_thumb.slice(0, 4).map((image, index) => (
                <img
                  style={{
                    width: "60px",
                    height: "60px",
                    objectFit: "cover",
                  }}
                  src={`/uploads/${image}`}
                  key={index}
                />
              ))}
            </div>
          ) : (
            <div></div>
          )}
        </div>
        <div className="detail_content">
          <div className="detail_content_customer">
            <p className="detail_title">Thng tin sn phm : </p>
            <div>
              <img src={require("../../assets/signature.png")} />
              <p>Tn sn phm : </p>
              <div className="detail_name">{product.product_name}</div>
            </div>
            <div>
              <img src={require("../../assets/shop.png")} />
              <p>Ca hng : </p>
              <div className="detail_gender">
                {product.product_shop.nameShop}
              </div>
            </div>
            <div>
              <img src={require("../../assets/price-tag.png")} />
              <p>Gi sn phm : </p>
              <div className="detail_email">
                {" "}
                {product.product_price.toLocaleString("vi-VN", {
                  style: "currency",
                  currency: "VND",
                })}
              </div>
            </div>
            <div>
              <img src={require("../../assets/attribute.png")} />
              <p>Thuc tnh : </p>
              <div className="detail_phone">
                {product.product_attributes.map((attribute, index) => (
                  <div key={index}>
                    <div
                      style={{
                        border: "1px solid #000",
                        borderRadius: "50%",
                        width: "50px",
                        height: "50px",
                        margin: "0 0.5em",
                        display: "flex",
                        justifyContent: "center",
                        backgroundColor: "#fff",
                      }}
                    >
                      <p style={{ textDecoration: "none", color: "#000" }}>
                        {attribute.color}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
            <div>
              <img src={require("../../assets/quantity.png")} />
              <p>S lng : </p>
              <div className="detail_phone">{product.product_quantity}</div>
            </div>
            <div>
              <img src={require("../../assets/star.png")} />
              <p>nh gi : </p>
              <div className="detail_gender">
                {product.product_ratingAverage}
              </div>
            </div>

            <div>
              <div>
                {" "}
                <img src={require("../../assets/three.png")} />
                <p>Chi tit sn phm : </p>
              </div>
            </div>
          </div>
          <div className="detail_gender">{product.product_description}</div>
          <div
            style={{
              marginTop: "15px",
            }}
          >
            {product.isPublished ? (
              <button
                style={{
                  width: "250px",
                  height: "35px",
                  color: "white",
                  border: "1px solid #e0e0e0",
                  backgroundColor: "red",
                }}
                onClick={() => showModal()}
              >
                n sn phm
              </button>
            ) : (
              <button
                style={{
                  width: "250px",
                  height: "35px",
                  color: "white",
                  backgroundColor: "gray",
                  border: "none",
                }}
                disabled={true}
                onClick={() => showModal()}
              >
                Sn phm  b n
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductDetail;
